// variables.less
$theme-primary: rgb(70, 140, 70);
$theme-dark: #222;

// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "mixins",
        "base"
;


body{
    background-image: url('/img/header.jpg');
    background-attachment: fixed;
}

section.pickup{
    clear: both;
    background: none;
    color:rgba(rgb(2, 32, 17),1);
}

#contact{
    background-color: white;
}

#footer{
    background-color: white;
    text-align: center;
}

//ナビゲーション項目名
#nav_usermamual,#nav_aitguide,#nav_aitsdk,#nav_restapi,#nav_contact,#nav_translate{
    font-size: 18px;
    color:rgba(rgb(2, 32, 17),1);
    
}

$sentence-background-color: rgba(0, 0, 0, 0.45);

//pickupの文字部分の縁取り
.pickup .sentence{
    background-color:$sentence-background-color;
    padding: 20px 40px;
    border-radius: 60px;
}

// 縁取りの外側のpadding
.pickup .container{
    padding: 40px 0px;
}

//ナビゲーションバーの画像のアニメーション
.nav-transition img{
    transition: width 300ms 0s ease;
}

//ナビゲーションバーのQunomonロゴ
.nav_qunomon{
    filter: drop-shadow(0 0 5px rgba(255, 255, 255, 1.0)); 
}

// スマホ版でのナビゲーションボタンの装飾
.navbar-default .navbar-collapse, .navbar-default .navbar-form{
    border: none;
}

//headerのQunomonロゴ
.header_qunomon{
    width: 60%;
    filter: drop-shadow(0 0 5px rgba(255, 255, 255, .6)); 
    padding-top: 50px;
}

//header内の文字修飾(デフォルトの設定を上書きするためにセレクタは詳細に書いています)
header .header-content .header-content-inner p.sentence{
    color: white;
    font-weight: 600;
    text-shadow: 1px 1px 0 black;
    background-color: $sentence-background-color;
    padding: 10px;
    border-radius: 20px;
}

//英語版の文章
header .header-content .header-content-inner p.sentence.english,
.pickup .english{
    font-family: 'Merriweather', 'Helvetica Neue', Arial, sans-serif;
    font-weight: 400;
}

//header内の文字修飾(英語版の太字)
header .header-content .header-content-inner p.sentence.english b,
header .header-content .header-content-inner p.sentence.english strong{
    font-size: 120%;
    font-weight: 900;
}

// header内のリンク
header .header-content .header-content-inner p.sentence a{
    color: white;
    font-weight: 900;
    position: relative;
    display: inline-block;
    text-decoration: none;
    border-bottom: 2px solid rgb(40, 80, 40);
}
header .header-content .header-content-inner p.sentence a:hover{
    color: rgb(100, 160, 100);
}

// col要素を中央(下)揃え
.row-center{
    text-align:center;
    letter-spacing:-0.4em;
}
.row-center > [class*='col-'] {
    display:inline-block;
    letter-spacing:0;
    vertical-align:top;
    float:none !important;
}
.row-center.va-middle > [class*='col-'] {
    vertical-align:middle;
}
.row-center.va-bottom > [class*='col-'] {
    vertical-align:bottom;
}

// 背景が白地の場合、文字を黒くする
.where-bg-white{
    color: black;
}